html {
  box-sizing: border-box;
}

fieldset {
  border: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.embed-container {
  max-width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
}

.embed-container iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

:root {
  --primary-color: #008bd1;
  --secondary-color: #0f1b25;
}

.colour--white {
  color: #fff !important;
}

.colour--primary {
  color: var(--primary-colour) !important;
}

.colour--secondary {
  color: var(--secondary-colour) !important;
}

.header {
  z-index: 9999999;
  background: #fff;
  width: 100%;
  transition: all .5s cubic-bezier(.165, .84, .44, 1);
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: -1px 4px 74px -16px #00000026;
}

.header--transparent {
  box-shadow: none;
  background: none;
}

.header--transparent + .banner {
  margin-top: 0;
}

.header.nav-down {
  background: #fff;
  transform: translateY(0%);
  box-shadow: -1px 4px 74px -16px #00000026;
}

.header.nav-up {
  transform: translateY(-140%);
}

.header__wrap {
  max-width: 1280px;
  height: 8rem;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
}

.header__wrap--full {
  max-width: 100%;
}

.header__wrap__menu {
  text-align: right;
  position: absolute;
  top: 50%;
  left: 0;
  right: 2rem;
  transform: translateY(-50%);
}

@media only screen and (width <= 1023px) {
  .header__wrap__menu {
    right: 7rem;
  }
}

.header__wrap__menu__search, .header__wrap__menu__item {
  vertical-align: middle;
  display: inline-block;
}

@media only screen and (width <= 1023px) {
  .header__wrap__menu__search:not(.header__wrap__menu__search), .header__wrap__menu__item:not(.header__wrap__menu__search) {
    display: none;
  }
}

.header__wrap__menu__search__link, .header__wrap__menu__item__link {
  color: #000;
  margin: 0;
  padding: 2rem .5rem;
  display: block;
  position: relative;
}

.header__wrap__menu__search__link p, .header__wrap__menu__item__link p {
  margin: 0;
}

.header__wrap__menu__search__link:hover .nav, .header__wrap__menu__search__link.section .nav, .header__wrap__menu__search__link.current .nav, .header__wrap__menu__item__link:hover .nav, .header__wrap__menu__item__link.section .nav, .header__wrap__menu__item__link.current .nav {
  color: var(--primary-colour);
}

.header__wrap__logo {
  z-index: 4;
  position: absolute;
  top: 50%;
  left: 2rem;
  transform: translateY(-50%);
}

.header__wrap__logo img {
  width: auto;
  height: 6rem;
}

.header__wrap__navburger {
  cursor: pointer;
  width: 3rem;
  height: 3rem;
  position: absolute;
  top: 1.8rem;
  right: -4.5rem;
}

@media only screen and (width >= 1024px) {
  .header__wrap__navburger {
    display: none;
  }
}

.header__wrap__navburger__ring {
  width: 3.6rem;
  height: .2rem;
  transition: all .333s;
  position: absolute;
}

.header__wrap__navburger__ring--1 {
  background: var(--primary-colour);
  margin-top: .5rem;
}

.header__wrap__navburger__ring--2 {
  background: var(--primary-colour);
  margin-top: 1.5rem;
}

.header__wrap__navburger__ring--3 {
  background: var(--primary-colour);
  margin-top: 2.5rem;
}

.header__wrap__nav__link__subnav {
  transform-origin: 50% 0;
  will-change: all;
  visibility: hidden;
  opacity: 0;
  perspective: 600px;
  width: 26rem;
  padding-top: 1.1rem;
  transition: all .333s;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: rotateX(-40deg)translateX(-50%);
}

.header__wrap__nav__link__subnav__wrap {
  background: #fff;
  border-top: 1px solid #f4f4f7;
  border-radius: 0 0 .2rem .2rem;
  padding: .8rem 0;
  box-shadow: 0 0 #0000, 0 0 #0000, 0 20px 25px -5px #0f172a1a, 0 8px 10px -6px #0f172a1a;
}

.header__wrap__nav__link__subnav__wrap__item {
  color: var(--primary-colour);
  text-align: left;
  padding: .5rem 2rem;
  font-size: 1.4rem;
  line-height: 1.4;
  text-decoration: none;
  transition: all .111s;
  display: block;
}

.header__wrap__nav__link__subnav__wrap__item p {
  margin: 0;
}

.header__wrap__nav__link__subnav__wrap__item.current, .header__wrap__nav__link__subnav__wrap__item:hover {
  background: var(--primary-colour);
}

.header__wrap__nav__link__subnav__wrap__item.current p, .header__wrap__nav__link__subnav__wrap__item:hover p {
  color: #fff;
  transition: all .111s;
}

.header__wrap__menu__item {
  perspective: 600px;
  display: inline-block;
  position: relative;
}

.header__wrap__menu__item:hover {
  z-index: 99;
}

.header__wrap__menu__item:hover .header__wrap__nav__link__subnav {
  opacity: 1;
  visibility: visible;
  transform: translateX(-50%);
}

